export default {
	name: 'I-Buttons',
	props: {
		disabled: {
			type: Boolean,
			default: false
		},
		loading: {
			type: Boolean,
			default: false
		},
		type: {
			type: String,
			default: 'default'
		}

	},
	methods:{
		handleClick(e) {
			this.$emit('click',e.target.value)
		}
	}
}
